// Firefox
* {
  scrollbar-width: rem(8);
  scrollbar-color: rgb(34,7,47) #d6d6d600;

  // Chrome, Edge, and Safari
  &::-webkit-scrollbar {
    width: rem(8);
    background-color: var(--white-color);
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 rem(6) rgba(#000, 20%);
    border-radius: rem(4);
    background: #eeeeee00;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: rem(4);
    border: 0 none #ffffff00;
    background-color: rgb(34,7,47);

    &:hover {
      background-color: rgb(40, 7, 56);
    }
  }
}