.work {
    position: relative;
    @include responsive(margin-top, 45, 160, 54);
    padding-top: vw(195);
    @include responsive(min-height, 1596, 2308, 1281);

    @media(max-width:1024px){
        padding-top: 0;
    }

    &__bg {
    }

    &__container {
        position: relative;
        z-index: 1;
    }

    &__title {
        @include responsive(margin-bottom, 80, 60, 36);

        @media(max-width:1024px){
            .mob {
                margin-left: auto;
                width: auto;
                min-width: vw(410, 768);
                margin-right: vw(-30, 768);
            }
            .mob:last-child {
                margin-left: 0;
                margin-right: 0;
                width: 100%;
            }
        }
        @media(max-width:480px){
            .mob {
                margin-right: vw(-10, 360);
                min-width: vw(205, 360);
            }
            .mob:last-child {
                margin-left: vw(-6, 360);
                margin-right: vw(-6, 360);
                width: 100%;
                min-width: vw(345, 360);
            }
        }
    }

    &__slider {
        @include responsive(margin-bottom, 40, 40, 24);
    }

    &__wrapper {
        display: flex;
        @include responsive(gap, 36, 39, 16);
        width: max-content;
        overflow-x: auto; /* Разрешаем горизонтальную прокрутку */
        overflow-y: hidden; /* Скрываем вертикальную прокрутку */

        @media(max-width:1024px){
            overflow: visible;
            width: 100%;
            flex-direction: column;
        }
    }

    &__slide {
        width: vw(562);
        @media(max-width:1024px){
            width: 100%;
        }
    }


    @media (min-width: 1025px) {
        [data-aos] {
          opacity: 1;
          transform: none; /* Убираем трансформацию */
          transition-property: none; /* Убираем анимацию */
        }
    }
}

.work-slide {

    &__title {
        display: flex;
        @include responsive(font-size, 44, 44, 24);
        @include responsive(gap, 16, 16, 8);
        line-height: 105%;
        text-transform: uppercase;
        @include responsive(margin-bottom, 8, 4, 8);
    }

    &__text {
        p {
            @include responsive(font-size, 32, 32, 18);

            @media(max-width:1024px){
                line-height: 134%;
            }
        }
    }
}


.work-auto {
    position: relative;
    transform: translateX(50%);

    @media(max-width:1024px){
        display: none;
    }
    .start & {
        animation: drive 3s ease-in-out 0s forwards;
    }
    
    &__img {
        width: vw(1920);
        margin-left: vw(-80);
        img {
            width: 100%;
        }
    }


    &__wheel {
        position: absolute;
        bottom: vw(227);
        left: vw(282);
        width: vw(273);
        height: vw(273);
        // opacity: 0.5;
    
        .start & {
            animation: rotationDrive 3s ease-in-out 0s forwards;
        }
        img {
            width: 100%;
            height: 100%;
        }

        &.two {
            left: auto;
            right: vw(213);
            bottom: vw(225);

            .work-auto__wheel-decor {
                width: vw(115);
                height: vw(115);
                top: vw(60);
                left: auto;
                right: vw(-2);

                .start & {
                    animation: decorAuto 0.5s ease 4s forwards;
                }
            }
        }
    }

    &__wheel-decor {
        position: absolute;
        width: vw(68);
        height: vw(68);
        top: vw(98);
        left: vw(34);
        opacity: 0;
        transform: scale(0.5);

        img {
            width: 100%;
            height: 100%;
        }

        .start & {
            animation: decorAuto 0.5s ease 3s forwards;
        }
    }

    &__turbo {
        position: absolute;
        width: vw(390);
        height: vw(108);
        right: vw(-327);
        bottom: vw(273);
        
        .start & {
            animation: smokeAnimation 4s ease-in-out 0s forwards;
        }
    }

    &__dialog {
        position: absolute;
        width: vw(282);
        height: vw(213);
        right: vw(475);
        top: vw(152);
        opacity: 0;
        transition: all 0.3s ease  0s;
        transform: scale(0.6);
        .start & {
            opacity: 1;
            transition: all 0.5s ease  5s;
            transform: scale(1);
            animation: shakeAnimation 3.5s ease-in-out 6s infinite;
        }
    }
}


.work-auto-mob {
    @media(min-width:1025px){
        display: none;
    }
    position: relative;
    width: 100vw;
    margin-left: vw(-36, 768);
    transition: all 1.5s ease  0s;
    left: vw(-120, 768);
    &.start-mob {
        left: 0;
    }
    @media(max-width:480px){
        margin-left: vw(-16, 360);
    }
    &__dialog {
        position: absolute;
        top: vw(40, 768);
        right: vw(192, 768);
        width: vw(204, 768);
        height: vw(155, 768);
        opacity: 0;
        transition: all 0.3s ease  0s;
        transform: scale(0.6);

        .start-mob & {
            opacity: 1;
            transition: all 0.5s ease  1.5s;
            transform: scale(1);
            animation: shakeAnimation 3.5s ease-in-out 1.5s infinite;
        }
        img {
            width: 100%;
        }
    }

    &__img {
        width: 100%;
        transform: scale(0.7);
        transition: all 1.5s ease  0s;

        .start-mob & {
            transform: scale(1);
            left: 0;
        }
        img {
            width: 100%;
        }
    }
}


@keyframes drive {
    0% {
        transform: translateX(50%);
    }
    100% {
        transform: translateX(0);
    }
}

@keyframes rotationDrive {
    from {
        transform: rotate(0);
    }
    to {
        transform: rotate(-360deg);
    }
}

@keyframes decorAuto{
    from {
        opacity: 0;
    }
    70% {
        opacity: 1;
        transform: scale(1.2);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes smokeAnimation {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    10% {
        opacity: 0.8;
        transform: scale(1.05);
    }
    20% {
        opacity: 0.9;
        transform: scale(1);
    }
    30% {
        opacity: 0.75;
        transform: scale(0.90) ;
    }
    40% {
        opacity: 1;
        transform: scale(1) ;
    }
    50% {
        opacity: 0.8;
        transform: scale(0.95);
    }
    60% {
        opacity: 0.9;
        transform: scale(1) ;
    }
    70% {
        opacity: 0.7;
        // transform: scale(0.96) translateX(1%);
    }
    80% {
        opacity: 1;
        // transform: scale(0.98) translateX(0.5%);
    }
    90% {
        opacity: 0.8;
        transform: scale(1);
    }
    100% {
        opacity: 0;
        transform: scale(1);
    }
}

@keyframes shakeAnimation {
    0%, 100% {
        transform: rotate(0deg);
    }
    5% {
        transform: rotate(-1deg);
    }
    10% {
        transform: rotate(2deg);
    }
    15% {
        transform: rotate(-2deg);
    }
    20% {
        transform: rotate(1deg);
    }
    25% {
        transform: rotate(0deg);
    }
    /* Пустое пространство в конце анимации до 4 секунд */
}