$mainOffset: 15; // отступы безопасности для контейнера
$maxWidth: 1920; // максимальная ширина страницы
$minWidth: 320; // минимальная ширина телефона
$containerWidth: 1760; // ширина контейнера из макета
$maxWidthContainer: $containerWidth + ($mainOffset * 2); // ширина контейнера с учетом padding слева и справа

$headerHeight: 100;
$footerHeight: 100;

:root {
  --bs-font-family: 'Pentagra', sans-serif; // Семейство шрифта по умолчанию
  --font-family: "Pentagra", sans-serif;
  --second-family: "Exo2", sans-serif;
  --third-family: "AlexisBulletItalic", sans-serif;
  --font3: "Spooky", sans-serif;
  --font4: "AlexisHalftoneItalic", sans-serif;
  --font5: "AlexisGradientItalic", sans-serif;

  --bs-font-size: 16px; // Размер шрифта по умолчанию
  --bs-font-weight: 400; // Жирность шрифта по умолчанию
  --bs-line-height: 120%; // Базовая высота шрифта
  --bs-font-color: #fff; // Цвет шрифта по умолчанию
  --bs-transition: .3s ease-in-out;
  --bs-radius: 10px;

  --primary-color: #131313;
  --primary-hover-color: #2d2d2d;
  --secondary-color: #ff4040;
  --white-color: #fff;
  --black-color: #131313;

  --bodi-1: #fff;
	--bodi-2: #fc83d4;
	--bodi-3: #15071f;
	--bodi-bg-page: #220736;

  --bs-shadow: 0px 2px 3px rgba(#000, 10%), 0px 8px 3px rgba(#000, 10%);
  --hover-shadow: 0 0 5px 0 rgba(#000, 10%),
                  0 0 10px 0 rgba(#000, 10%),
                  0 0 15px 0 rgba(#000, 10%);
}