.cases {
    @include responsive(padding-top, 270, 160, 80);

    &__container {
    }

    &__head {
        display: flex;
        justify-content: space-between;
        @include responsive(margin-bottom, 80, 63, 36);

        @media(max-width:1024px){
            flex-direction: column;
            align-items: center;
            gap: vw(60, 768);
        }

        @media(max-width:480px){
            gap: vw(36, 360);
        }
    }

    &__title {
        display: flex;
        flex-direction: column;
        text-align-last: left;
        @include responsive(gap, 10, 10, 5);
        @include responsive(min-width, 820, 696, 328);

        @media(max-width:1024px){
            text-align: center;
            align-items: center;
            text-align-last: center;
        }

        .title-hal {
            @include responsive(font-size, 180, 120, 60);
            width: 100%;
        }

        .title-bul {
            @include responsive(font-size, 120, 80, 44);
            width: 100%;
        }
    }

    &__text {

        @include responsive(font-size, 44, 44, 24);
        line-height: 120%;
        text-transform: uppercases;
        min-width: vw(862);

        @media(max-width:1024px){
            min-width: 0;
        }
    }

    &__text-one {
        display: flex;
        justify-content: space-between;
        gap: vw(10);
    }

    &__text-two {
    }

    &__text-three {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: vw(10);
        p:last-child {
            margin-left: auto;
        }
    }
}

.cases-body {
    @include responsive(min-height, 1586, 1562, 984);
    position: relative;
    &__bg {
        img,picture {
            width: 100%;
            height: 100%;
        }
    }

    &__row {
        position: relative;
        z-index: 1;
        @include responsive(padding-top, 68, 68, 40);
        @include responsive(padding-right, 60, 36, 12);
        @include responsive(padding-left, 60, 36, 12);
        display: flex;
        flex-direction: column;
        @include responsive(gap, 36, 16, 8);
    }

    &__head {
        position: relative;
        @include responsive(min-height, 270, 274, 148);
    }

    &__head-bg {
    }

    &__head-body {
        position: relative;
        z-index: 2;
        @include responsive(padding-top, 24, 24, 12);
        @include responsive(padding-bottom, 60, 60, 31);
        @include responsive(padding-right, 24, 24, 12);
        @include responsive(padding-left, 24, 24, 12);
        // max-height:vw(270);
        
    }

    &__slider {
        @include responsive(padding-top, 44, 24, 12);
        @include responsive(padding-bottom, 44, 24, 11);
        @include responsive(padding-right, 44, 24, 12);
        @include responsive(padding-left, 44, 24, 12);
        
        @include responsive(max-height, 180, 190, 105);
        overflow: hidden;
    }

    &__wrapper {
    }

    &__slide {
        width: 100%;
        display: flex;
        align-items: start;
        justify-content: space-between;
        @include responsive(gap, 12, 12, 7);

        @media(max-width:1024px){
            flex-direction: column;
            align-items: start;
        }
    }

    &__head-left {
        display: flex;
        align-items: center;
        @include responsive(gap, 36, 24, 8);
    }

    &__head-img {
        @include responsive(width, 196, 166, 68);
        svg {
            width: 100%;
            height: auto;
        }
    }

    &__head-title {
        @include responsive(max-width, 332, 336, 180);
        @include responsive(font-size, 44, 44, 24);
        text-transform: uppercase;
        line-height: 120%;
    }

    &__head-right {
        max-width: vw(730);
        @media(max-width:1024px){
            max-width: 100%;
            margin-right: vw(-22, 768);
            margin-left: vw(-23, 768);
            min-width: vw(571, 768);
            opacity: 0;
            transition: all 0.3s ease-in-out  0s;

            .swiper-slide-active & {
                transition: all 0.3s ease-in-out  1s;
                opacity: 1;
            }
        }
        @media(max-width:480px){
            margin-right: vw(-11, 360);
            margin-left: vw(-11, 360);
            min-width: vw(277.5, 360);
        }

        p {
            @include responsive(font-size, 24, 24, 16);
            text-transform: uppercase;
            line-height: 125%;
            white-space: wrap;
            @media(max-width:1024px){
                display: inline-block;
                min-width: 100%;
                white-space: nowrap;
            }
        }
    }

    &__pagination  {
        display: flex;
        top: auto !important;
        bottom: 0;
        transform: translateX(50%) translateY(0) !important;
        right: 50% !important;
        // transform: translateX(50%);
        .swiper-pagination-bullet {
            @include responsive(width, 120, 80, 44);
            @include responsive(height, 12, 12, 6);
            border-radius: 0;
            background: linear-gradient(359deg, #261332 0%, #412065 100%);
            box-shadow: 0 0 6px 0 #5f3a8a, inset 0 2px 3px 0 #1f1431;
            opacity: 1;
            transition: all 0.3s ease  0s;

            &:nth-child(1) {
                clip-path: polygon(0% 0%, 91.67% 0%, 100% 100%, 8.33% 100%);
            }
            &:nth-child(2) {
                clip-path: polygon(0% 0%, 100% 0%, 90% 100%, 10% 100%);
            }

            &:nth-child(3) {
                clip-path: polygon(0% 0%, 91.67% 0%, 100% 100%, 8.33% 100%);
                transform: scaleY(-1);
            }
        }
        .swiper-pagination-bullet-active {
            background: linear-gradient(180deg, #1b4cfa 0%, #f5a1fc 100%);
            background: rgb(163,129,250);
            box-shadow: 0 0 16px  #2602ff;
        }
    }

    &__item-row {
        display: flex;
        flex-direction: column;
        @include responsive(gap, 36, 16, 8);
    }
}



.item-cases {
    position: relative;
    @include responsive(height, 196, 212, 140);
    &:hover  &__img .two {
        -webkit-animation-duration:2s;
        animation-duration:2s;
        -webkit-animation-timing-function:linear;
        animation-timing-function:linear;
        // -webkit-animation-iteration-count:infinite;
        // animation-iteration-count:infinite;
        -webkit-animation-name:glitch-anim-two;
        animation-name:glitch-anim-two;
        -webkit-animation-direction:alternate;
        animation-direction:alternate;
    }
    &__bg {
        height: 100%;
        opacity: 0.8;
        svg {
            width: 100%;
            height: 100%;
        }

        .web {
            @media(max-width:1024px){
                display: none;
            }
        }
        .tab {
            display: none;
            @media(max-width:1024px){
                display: block;
            }
            @media(max-width:480px){
                display: none;
            }
        }
        .mob {
            display: none;
            @media(max-width:480px){
                display: block;
            }
        }
    }
    &__body {
        position: relative;
        z-index: 1;
        @include responsive(padding-left, 44, 24, 12);
        @include responsive(padding-right, 60, 44, 12);
        @include responsive(padding-top, 24, 24, 12);
        @include responsive(padding-bottom, 24, 24, 16);
        display: flex;
        @include responsive(gap, 36, 24, 14);
        overflow: hidden;
        height: 100%;
        @media(max-width:480px){   
            flex-direction: column;
        }
    }

    &__img {
        position: relative;
        margin-bottom: vw(-24);
        @include responsive(margin-left, -50, -64, 0);
        @include responsive(margin-right, -36, -36, 0);
        @include responsive(width, 300, 290, 140);
        @include responsive(min-width, 300, 290, 140);
        margin-top: auto;
        img {
            width: 100%;
        }
        .two {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            opacity: 0;
            transition: all 0.3s ease  0s;
        }
        @media(max-width:1024px){
            margin-bottom: vw(-24, 768);
        }
        @media(max-width:480px){

            position: absolute;
            margin: 0;
            bottom: 0;
            left: vw(-18, 360);
        }
    }

    &__text {
      
    }

    &__title {
        font-family: var(--font3);
        font-weight: 400;
        font-size: 80px;
        @include responsive(font-size, 80, 60, 36);
        @include responsive(margin-bottom, 16, 8, 4);
        line-height: 105%;
        text-transform: uppercase;
        color: var(--bodi-2);
        // font-family: Spooky;
        text-shadow: 0 0 24px #ff029a, 0 4px 4px #e32e9b;

        @media(max-width:1024px){
            line-height: 114%;
            margin-right: -100%;
        }
    }

    &__text-row {
        display: flex;
        @include responsive(gap, 24, 4, 2);
        p {
            @include responsive(font-size, 24, 24, 12);
            span {
                display: inline-block;
                opacity: 0.4;
                @include responsive(margin-right, 8, 8, 4);
            }
        }

        @media(max-width:1024px){
            flex-direction: column;
            margin-right: -8%;
        }

        @media(max-width:480px){
            padding-left: vw(139, 360);
        }
    }

    &__button-row {
        margin-left: auto;
        display: flex;
        align-items: center;

        @media(max-width:1024px){
            align-items: end;
            padding-bottom: vw(25, 768);
        }
        @media(max-width:480px){
            padding-bottom: 0;
        }
    }
}
.g-text {
}


.btn-item {
    display: block;
    position: relative;
    width: vw(206);
    height: vw(68);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    @media(max-width:1024px){
        width: auto;
        height: auto;
    }
    &:hover {
        span {
            transform: translateX(vw(17))
        }
        .btn-item__str {
            transform: translateX(vw(17));
            opacity: 0;
        }
    }

    @include responsive(font-size, 24, 24, 16);
    &__bg {
        svg {
            width: 100%;
            height: auto;
        }
        @media(max-width:1024px){
            display: none;
        }
    }

    &__row {
        position: relative;
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        @include responsive(gap, 8, 4, 4);
        span {
            font-family: var(--second-family);
            font-weight: 700;
            line-height: 120%;
            text-transform: uppercase;
            color: var(--bodi-1);
            text-shadow: 0 0 3px #2602ff;
            transition: all 0.3s ease  0s;
        }
    }

    &__str {
        @include responsive(width, 32, 32, 18);
        @include responsive(height, 22, 22, 12.5);
        transition: all 0.3s ease  0s;

        svg {
            width: 100%;
            height: auto;
        }
    }
}


@keyframes glitch-anim-two {
    0% {
     opacity:0;
     -webkit-transform:translateZ(0);
     transform:translateZ(0);
     -webkit-clip-path:polygon(0 2%,100% 2%,100% 5%,0 5%);
     clip-path:polygon(0 2%,100% 2%,100% 5%,0 5%)
    }
    2% {
     -webkit-clip-path:polygon(0 78%,100% 78%,100% 100%,0 100%);
     clip-path:polygon(0 78%,100% 78%,100% 100%,0 100%);
     -webkit-transform:translate(-5px);
     transform:translate(-5px)

    }
    6% {
     -webkit-clip-path:polygon(0 78%,100% 78%,100% 100%,0 100%);
     clip-path:polygon(0 78%,100% 78%,100% 100%,0 100%);
     -webkit-transform:translate(5px);
     transform:translate(5px)
    }
    8% {
     -webkit-clip-path:polygon(0 78%,100% 78%,100% 100%,0 100%);
     clip-path:polygon(0 78%,100% 78%,100% 100%,0 100%);
     -webkit-transform:translate(-5px);
     transform:translate(-5px)
    }
    9% {
     -webkit-clip-path:polygon(0 78%,100% 78%,100% 100%,0 100%);
     clip-path:polygon(0 78%,100% 78%,100% 100%,0 100%);
     -webkit-transform:translate(0);
     transform:translate(0);
     opacity: 1;
    }
    10% {
     -webkit-clip-path:polygon(0 54%,100% 54%,100% 44%,0 44%);
     clip-path:polygon(0 54%,100% 54%,100% 44%,0 44%);
     -webkit-transform:translate3d(5px,0,0);
     transform:translate3d(5px,0,0)
    }
    13% {
     -webkit-clip-path:polygon(0 54%,100% 54%,100% 44%,0 44%);
     clip-path:polygon(0 54%,100% 54%,100% 44%,0 44%);
     -webkit-transform:translateZ(0);
     transform:translateZ(0)
    }
    13.1% {
     -webkit-clip-path:polygon(0 0,0 0,0 0,0 0);
     clip-path:polygon(0 0,0 0,0 0,0 0);
     -webkit-transform:translate3d(5px,0,0);
     transform:translate3d(5px,0,0)
    }
    15% {
     -webkit-clip-path:polygon(0 60%,100% 60%,100% 40%,0 40%);
     clip-path:polygon(0 60%,100% 60%,100% 40%,0 40%);
     -webkit-transform:translate3d(5px,0,0);
     transform:translate3d(5px,0,0)
    }
    20% {
     -webkit-clip-path:polygon(0 60%,100% 60%,100% 40%,0 40%);
     clip-path:polygon(0 60%,100% 60%,100% 40%,0 40%);
     -webkit-transform:translate3d(-5px,0,0);
     transform:translate3d(-5px,0,0)
    }
    20.1% {
     -webkit-clip-path:polygon(0 0,0 0,0 0,0 0);
     clip-path:polygon(0 0,0 0,0 0,0 0);
     -webkit-transform:translate3d(5px,0,0);
     transform:translate3d(5px,0,0)
    }
    25% {
     -webkit-clip-path:polygon(0 85%,100% 85%,100% 40%,0 40%);
     clip-path:polygon(0 85%,100% 85%,100% 40%,0 40%);
     -webkit-transform:translate3d(5px,0,0);
     transform:translate3d(5px,0,0)
    }
    30% {
     -webkit-clip-path:polygon(0 85%,100% 85%,100% 40%,0 40%);
     clip-path:polygon(0 85%,100% 85%,100% 40%,0 40%);
     -webkit-transform:translate3d(-5px,0,0);
     transform:translate3d(-5px,0,0)
    }
    30.1% {
     -webkit-clip-path:polygon(0 0,0 0,0 0,0 0);
     clip-path:polygon(0 0,0 0,0 0,0 0)
    }
    35% {
     -webkit-clip-path:polygon(0 63%,100% 63%,100% 80%,0 80%);
     clip-path:polygon(0 63%,100% 63%,100% 80%,0 80%);
     -webkit-transform:translate(-5px);
     transform:translate(-5px)
    }
    40% {
     -webkit-clip-path:polygon(0 63%,100% 63%,100% 80%,0 80%);
     clip-path:polygon(0 63%,100% 63%,100% 80%,0 80%);
     -webkit-transform:translate(5px);
     transform:translate(5px)
    }
    45% {
     -webkit-clip-path:polygon(0 63%,100% 63%,100% 80%,0 80%);
     clip-path:polygon(0 63%,100% 63%,100% 80%,0 80%);
     -webkit-transform:translate(-5px);
     transform:translate(-5px)
    }
    50% {
     -webkit-clip-path:polygon(0 63%,100% 63%,100% 80%,0 80%);
     clip-path:polygon(0 63%,100% 63%,100% 80%,0 80%);
     -webkit-transform:translate(0);
     transform:translate(0)
    }
    55% {
     -webkit-clip-path:polygon(0 10%,100% 10%,100% 0,0 0);
     clip-path:polygon(0 10%,100% 10%,100% 0,0 0);
     -webkit-transform:translate3d(5px,0,0);
     transform:translate3d(5px,0,0)
    }
    60% {
     -webkit-clip-path:polygon(0 10%,100% 10%,100% 0,0 0);
     clip-path:polygon(0 10%,100% 10%,100% 0,0 0);
     -webkit-transform:translateZ(0);
     transform:translateZ(0);
     opacity:1
    }
    60.1% {
     -webkit-clip-path:polygon(0 0,0 0,0 0,0 0);
     clip-path:polygon(0 0,0 0,0 0,0 0);
     opacity:1
    }
    to {
     -webkit-clip-path:polygon(0 0,0 0,0 0,0 0);
     clip-path:polygon(0 0,0 0,0 0,0 0);
     opacity:0
    }
   }